<template>
	<view class="body">
		<view class="head">
			<view class="head_back"><image src="@/static/img/back.png" mode="" @click="navigateBack()"></image></view>
			<view class="head_center hei_38_bold">咨询记录</view>
			<view class=" head_right"><image src="@/static/lsimg/sousuo.png" mode="" @click="tan"></image></view>
		</view>
		<view class="tab_top hui_26">
			<text v-for="(item, index) in tab_arry" :class="['', index == active ? 'hong_26_bold' : '']" @click="qiehuan(index)">{{ item }}</text>
		</view>
		<view class="zi_body tab_zi_body">
			<view class="zixun_list">
				<view class="zixun_item" v-for="(item, index) in 10">
					<view class="zixun_item_left">
						<view class="zixun_item_tx"><image src="@/static/lsimg/tx.png" mode=""></image></view>
						<view class="zixun_item_xinxi">
							<view class="hei_26">18147514420</view>
							<view class="hong_26 zixun_item_feiyong">￥58.00/24小时</view>
							<view class="qian_22">2020-10-12 13:19:04</view>
						</view>
					</view>

					<view :class="['item_zhuangtai bai_26', 0 == zhuangtai ? 'jieshu' : 'zhengzai']">{{ zhuangtai == 0 ? '咨询结束' : '正在咨询' }}</view>
				</view>
			</view>
		</view>

		<uni-popup type="bottom" ref="popup">
			<view class="tan">
				<view class="tan_title hei_30">查询条件</view>
				<view class="tan_list hei_26">
					<view class="tan_list_top">
						<text>付费金额</text>
						<input type="text" value="" placeholder="例如：￥58.00" v-model="jine" />
					</view>
				</view>
				<view class="tan_list hei_26">
					<view class="tan_list_top">
						<text>用户名称</text>
						<input type="text" value="" placeholder="张三" v-model="name" />
					</view>
				</view>

				<view class="tan_buttom ">
					<button type="" class="quxiao hong_30" @click="quxiao_btn">取消搜索</button>
					<button type="" class="queren bai_30" @click="queding_btn">确认搜索</button>
				</view>
			</view>
		</uni-popup>
	</view>
</template>

<script>
import unpopup from '@/components/uni-popup/uni-popup-share.vue';

export default {
	components: {
		unpopup
	},

	onShow() {},

	data() {
		return {
			tab_arry: ['全部咨询', '正在咨询', '结束咨询'],
			active: '0',
			zhuangtai: '0',
			jine: '',
			name: ''
		};
	},
	created() {},
	//下拉刷新
	onPullDownRefresh: function() {
		// this.getList();

		setTimeout(function() {
			uni.showToast({
				title: '没有更多内容了',
				duration: 2000,
				icon: 'none'
			});
			uni.stopPullDownRefresh();
		}, 1000);
	},
	// stopPullDownRefresh:function(){

	// },
	methods: {
		//上拉加载
		onReachBottom() {
			uni.showToast({
				title: '没有更多内容了',
				duration: 2000,
				icon: 'none'
			});
		},
		navigateBack() {
			uni.navigateBack();
		},
		// 切换tab
		qiehuan(index) {
			this.active = index;
			if (index == 1) {
				this.zhuangtai = 1;
			} else {
				this.zhuangtai = 0;
			}
		},
		huifu() {
			// this.$refs.huifu.open()
		},
		// 底部弹窗
		tan() {
			this.$refs.popup.open();
		},

		// 取消搜索
		quxiao_btn() {
			this.jine = '';
			this.name = '';
			this.$refs.popup.close();
		},
		// 确定搜索
		queding_btn() {
			console.log(this.jine, this.name);
			this.$refs.popup.close();
		}
	}
};
</script>

<style>
page {
	background-color: #f7f7f7;
	min-height: 100%;
}

.tab_top {
	display: flex;
	align-items: center;
	justify-content: space-between;
	padding: 10rpx 38rpx;
	background-color: #ffffff;
	position: fixed;
	top: 100rpx;
	width: 100%;
	box-sizing: border-box;
	z-index: 9;
}

.head_right image {
	width: 40rpx;
	height: 41rpx;
}

.zixun_item_tx image {
	width: 80rpx;
	height: 81rpx;
	box-sizing: border-box;
	margin-right: 18rpx;
}

.zixun_item {
	display: flex;
	align-items: center;
	height: 191rpx;
	background-color: #ffffff;
	margin-bottom: 20rpx;
	justify-content: space-between;
	padding: 0 49rpx 0 30rpx;
	box-sizing: border-box;
}

.zixun_item:first-child {
	margin-top: 20rpx;
}

.zixun_item_left {
	display: flex;
}

.zixun_item_feiyong {
	margin: 18rpx 0;
}

.item_zhuangtai {
	width: 164rpx;
	height: 50rpx;

	border-radius: 5rpx;
	-moz-transform: rotate(-10deg);
	-webkit-transform: rotate(-10deg);
	text-align: center;
	line-height: 46rpx;
}

.zhengzai {
	background-color: #f43a51;
}

.jieshu {
	color: #999999;
	background-color: #dee0e2;
}

.tan {
	width: 100%;
	height: 758rpx;
	background-color: #ffffff;
	padding: 0 23rpx;
	box-sizing: border-box;
	position: relative;
}

.tan_title {
	text-align: center;
	line-height: 120rpx;
}

.tan_list {
}

.tan_list_top {
	display: flex;
	align-items: center;
	border-bottom: 2rpx solid #e8e8e8;
	height: 98rpx;
	justify-content: space-between;
}

.tan_list input {
	text-align: right;
}

.tan_buttom {
	display: flex;
	align-items: center;
	position: absolute;
	bottom: 120rpx;
}

button {
	margin: 0;
	border: none;
}

button::after {
	content: none;
}

.quxiao {
	width: 342rpx;
	height: 88rpx;
	background-color: #ffd7dc;
	border-radius: 44rpx 0rpx 0rpx 44rpx;
	line-height: 88rpx;
}

.queren {
	width: 342rpx;
	height: 88rpx;
	line-height: 88rpx;
	background-color: #f43a51;
	border-radius: 0rpx 44rpx 44rpx 0rpx;
}
</style>
